/*=============================================
=            Theme default CSS            =
=============================================*/
/*-- Google Font --*/
@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Sans:300,400,500,600,700|Montserrat:400,600,700");
*, *::after, *::before {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  line-height: 20px;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  visibility: visible;
  font-family: "IBM Plex Sans", sans-serif;
  color: #333333;
  position: relative;
  background-color: #ffffff;
}

h1, h2, h3, h4, h5, h6 {
  color: #333333;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 600;
  margin-top: 0;
}

h1 {
  font-size: 36px;
  line-height: 42px;
}

h2 {
  font-size: 30px;
  line-height: 36px;
}

h3 {
  font-size: 24px;
  line-height: 30px;
}

h4 {
  font-size: 18px;
  line-height: 24px;
}

h5 {
  font-size: 14px;
  line-height: 18px;
}

h6 {
  font-size: 12px;
  line-height: 14px;
}

p:last-child {
  margin-bottom: 0;
}

a, button {
  color: inherit;
  display: inline-block;
  line-height: inherit;
  text-decoration: none;
  cursor: pointer;
}

a, button, img, input, span {
  transition: all 0.1s ease 0s;
}

*:focus {
  outline: none !important;
}

a:focus {
  color: inherit;
  outline: none;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  color: #f0475c;
}

button, input[type=submit] {
  cursor: pointer;
}

ul {
  list-style: outside none none;
  margin: 0;
  padding: 0;
}

/*-- Tab Content & Pane Fix --*/
.tab-content {
  width: 100%;
}
.tab-content .tab-pane {
  display: block;
  height: 0;
  max-width: 100%;
  visibility: hidden;
  overflow: hidden;
  opacity: 0;
}
.tab-content .tab-pane.active {
  height: auto;
  visibility: visible;
  opacity: 1;
  overflow: visible;
}

.border-bottom {
  border-bottom: 1px solid #ededed;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  padding-right: 15px;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' version='1.1' height='10px' width='15px'%3E%3Ctext x='0' y='10' fill='black'%3E%E2%96%BE%3C/text%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: 95% 50%;
}

/*----------  Sharp border tippyjs theme  ----------*/
/* If `animateFill: true` (default) */
.tippy-tooltip.sharpborder-theme .tippy-backdrop {
  background-color: #3EB65B;
  font-weight: 400;
  color: #fff;
}

/* If `animateFill: false` */
.tippy-tooltip.sharpborder-theme {
  font-weight: 400;
  font-size: 12px;
  background-color: #3EB65B;
  color: #fff;
  line-height: 12px;
  white-space: nowrap;
  padding: 8px 10px;
}

.tippy-popper[x-placement^=top] .tippy-tooltip.sharpborder-theme .tippy-arrow {
  border-top-color: #3EB65B;
}

/*=====  End of Theme default CSS  ======*/
/*=============================================
=            Header            =
=============================================*/
/*----------  header area  ----------*/
.header-area {
  position: relative;
  z-index: 9;
  /* absolute header style */
}
.header-area.is-sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  animation: 700ms ease-in-out 0s normal none 1 running fadeInDown;
}
.header-area.is-sticky .navigation-top {
  display: none;
}
.header-area.is-sticky .navigation-menu-area {
  background: rgba(255, 255, 255, 0.7);
}
.header-area.is-sticky .navigation-menu-area:hover {
  background-color: #ffffff;
}
.header-area--absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .header-area--absolute {
    position: relative;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area--absolute {
    position: relative;
  }
}
@media only screen and (max-width: 767px) {
  .header-area--absolute {
    position: relative;
  }
}
.header-area--absolute.is-sticky--absolute {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  animation: 700ms ease-in-out 0s normal none 1 running fadeInDown;
  background-color: rgba(241, 241, 239, 0.7);
}
.header-area--absolute.is-sticky--absolute:hover {
  background-color: #f1f1ef;
}
.header-area--absolute.is-sticky--absolute .navigation-top-topbar {
  display: none;
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area--absolute.is-sticky--absolute .navigation-top-search-area {
    margin-top: 0 !important;
  }
}
@media only screen and (max-width: 767px) {
  .header-area--absolute.is-sticky--absolute .navigation-top-search-area {
    margin-top: 0 !important;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area--absolute.is-sticky--absolute .logo {
    display: none;
  }
}
@media only screen and (max-width: 767px) {
  .header-area--absolute.is-sticky--absolute .logo {
    display: none;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area--absolute.is-sticky--absolute .search-icon {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .header-area--absolute.is-sticky--absolute .search-icon {
    display: none !important;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .header-area--absolute.is-sticky--absolute .header-cart-icon {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .header-area--absolute.is-sticky--absolute .header-cart-icon {
    display: none !important;
  }
}
.header-area--absolute.is-sticky--absolute .main-menu--separate nav > ul > li > a {
  line-height: 70px;
}

/*----------  navigatopn top  ----------*/
.navigation-top {
  background-color: #3EB65B;
}

/*----------  navigation top topbar  ----------*/
.navigation-top-topbar {
  border-bottom: 1px solid rgba(255, 255, 255, 0.17);
}

/*----------  header top social links  ----------*/
@media only screen and (max-width: 767px) {
  .header-top-social-links {
    border-bottom: 1px solid rgba(255, 255, 255, 0.17);
    padding-bottom: 10px;
  }
}

/*----------  headertop dropdown container  ----------*/
.headertop-dropdown-container {
  display: flex;
  justify-content: flex-end;
}
@media only screen and (max-width: 767px) {
  .headertop-dropdown-container {
    padding-top: 10px;
  }
}
@media only screen and (max-width: 479px) {
  .headertop-dropdown-container:first-child .extra-small-mobile-fix {
    left: 0;
  }
}
.headertop-dropdown-container .header-top-single-dropdown {
  position: relative;
}
.headertop-dropdown-container .header-top-single-dropdown a {
  color: #ffffff;
  font-weight: 300;
  font-size: 14px;
}
.headertop-dropdown-container .header-top-single-dropdown a:hover {
  color: #ffdd20;
}
.headertop-dropdown-container .header-top-single-dropdown a i {
  font-size: 11px;
  margin-left: 3px;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items {
  position: absolute;
  left: auto;
  right: -15%;
  top: 165%;
  background-color: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  padding: 0 20px;
  min-width: 160px;
  z-index: 5;
  display: none;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items.deactive-dropdown-menu {
  display: none;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items.active-dropdown-menu {
  display: block;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items ul li {
  border-bottom: 1px solid #eeeeee;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items ul li:last-child {
  border-bottom: 0;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items ul li a {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 13px;
  color: #666;
  font-weight: 300;
  line-height: 2.2;
  padding: 10px 0;
  display: block;
}
.headertop-dropdown-container .header-top-single-dropdown__dropdown-menu-items ul li a:hover {
  color: #3EB65B;
}
.headertop-dropdown-container span.separator {
  margin: 0 15px;
  color: #ffffff;
}

/*----------  navigation top search  ----------*/
/*----------  search bar  ----------*/
.search-bar {
  position: relative;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .search-bar {
    transform: translateX(30px);
    width: 95%;
  }
}
.search-bar form input {
  width: 100%;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  color: #ffffff;
  font-weight: 300;
  background: rgba(255, 255, 255, 0.2);
  border: 0;
  padding: 0 65px 0 20px;
  height: 48px;
  border-radius: 5px;
}
.search-bar form button {
  position: absolute;
  top: 0;
  right: 0;
  background: transparent;
  border: 0;
  width: 60px;
  padding: 0;
  box-shadow: none !important;
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 22px;
  color: #ffffff;
  font-weight: 400;
  line-height: 48px;
}
.search-bar form button:hover {
  color: #ffdd20;
}
.search-bar form ::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff;
  opacity: 1;
  /* Firefox */
}
.search-bar form :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #ffffff;
}
.search-bar form ::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #ffffff;
}

/*----------  customer support text  ----------*/
.customer-support-text {
  position: relative;
  transform: translateX(50px);
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .customer-support-text {
    transform: translateX(70px);
  }
}
@media only screen and (max-width: 767px) {
  .customer-support-text {
    transform: none;
  }
}
@media only screen and (max-width: 575px) {
  .customer-support-text {
    display: inline-block;
  }
}
.customer-support-text .icon {
  position: absolute;
  left: 0;
  top: 5px;
}
.customer-support-text .text {
  padding-left: 52px;
}
@media only screen and (max-width: 575px) {
  .customer-support-text .text {
    text-align: left;
  }
}
.customer-support-text .text span {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 13px;
  color: #ffffff;
  font-weight: 300;
  line-height: 18px;
}
.customer-support-text .text p {
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 18px;
  color: #ffdd20;
  font-weight: 700;
  line-height: 25px;
}

/*----------  small cart  ----------*/
.header-cart-icon {
  position: relative;
}
.header-cart-icon .small-cart-trigger {
  border: 2px solid #ffffff;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 27px;
  color: #ffffff;
  font-weight: 400;
  line-height: 46px;
  padding-left: 8px;
  padding-right: 27px;
  display: inline-block;
  position: relative;
  border-radius: 15px;
}
.header-cart-icon .small-cart-trigger .icon-shopping-cart {
  margin-right: 7px;
  color: #ffffff;
}
.header-cart-icon .small-cart-trigger .cart-counter {
  width: 16px;
  height: 16px;
  border-radius: 100%;
  font-style: normal;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 11px;
  color: #333;
  font-weight: 400;
  line-height: 16px;
  background: #fff;
  position: absolute;
  left: 45px;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}
.header-cart-icon .small-cart-trigger:hover, .header-cart-icon .small-cart-trigger.active {
  color: #333;
  background: #fff;
  border-color: #fff;
}
.header-cart-icon .small-cart-trigger:hover .cart-counter, .header-cart-icon .small-cart-trigger.active .cart-counter {
  background: #3EB65B;
  color: #ffffff;
}

.small-cart {
  position: absolute;
  width: 380px;
  right: 0;
  left: auto;
  background-color: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  margin-top: 26px;
  display: none;
  z-index: 9;
}
@media only screen and (max-width: 479px) {
  .small-cart {
    max-width: 300px;
  }
}

.small-cart-item-wrapper {
  padding: 30px;
  border-bottom: 1px solid #ebebeb;
}
.small-cart-item-wrapper .single-item {
  display: flex;
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ebebeb;
  position: relative;
}
.small-cart-item-wrapper .single-item:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
  border-bottom: 0;
}
.small-cart-item-wrapper .single-item .image {
  flex-basis: 80px;
}
.small-cart-item-wrapper .single-item .remove-icon {
  position: absolute;
  top: 0;
  right: 0;
}
.small-cart-item-wrapper .single-item .remove-icon i {
  font-size: 12px;
}
.small-cart-item-wrapper .single-item .remove-icon:hover {
  color: #3EB65B;
}
.small-cart-item-wrapper .single-item .content {
  flex-basis: calc(100% - 80px);
  padding-left: 10px;
  text-align: left;
}
.small-cart-item-wrapper .single-item .content p {
  margin-bottom: 0;
}
.small-cart-item-wrapper .single-item .content p.cart-name a {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 15px;
  color: #333;
  font-weight: 300;
  line-height: 20px;
  margin-bottom: 8px;
}
.small-cart-item-wrapper .single-item .content p.cart-name a:hover {
  color: #3EB65B;
}
.small-cart-item-wrapper .single-item .content p.cart-quantity {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 16px;
  color: #3EB65B;
  font-weight: 700;
  line-height: 18px;
}
.small-cart-item-wrapper .single-item .content p.cart-quantity .quantity-mes {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 13px;
  color: #333;
  font-weight: 300;
  line-height: 18px;
}

.cart-calculation-table {
  padding: 30px;
}
.cart-calculation-table table tbody tr:last-child td:last-child {
  color: #3EB65B;
  font-size: 22px;
}
.cart-calculation-table table tbody tr td {
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  padding-bottom: 0;
  border-top: 0;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 18px;
  color: #333;
  font-weight: 600;
  line-height: 30px;
}

.cart-buttons a {
  display: block;
  margin-bottom: 10px;
}
.cart-buttons a:last-child {
  margin-bottom: 0;
}

/*----------  active and deactive dropdown  ----------*/
.deactive-dropdown-menu {
  display: none;
}

.active-dropdown-menu {
  display: block;
}

/*----------  navigation menu  ----------*/
.navigation-menu-area {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}
.navigation-menu-area .mobile-menu-wrapper {
  background-color: #3EB65B;
}

.main-menu {
  padding-top: 6px;
  padding-bottom: 6px;
  /* separate menu style */
}
.main-menu nav > ul > li {
  display: inline-block;
  position: relative;
  margin-right: 50px;
}
.main-menu nav > ul > li:last-child {
  margin-right: 0;
}
.main-menu nav > ul > li.active > a, .main-menu nav > ul > li:hover > a {
  color: #3EB65B;
  position: relative;
}
.main-menu nav > ul > li.menu-item-has-children > a::after {
  font-family: Ionicons;
  content: "";
  margin-left: 5px;
  float: right;
  font-size: 11px;
  font-weight: 300;
}
.main-menu nav > ul > li > a {
  display: block;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  color: #333;
  font-weight: 600;
  line-height: 24px;
  font-family: "Montserrat", sans-serif;
  line-height: 55px;
}
.main-menu nav > ul > li:hover > .sub-menu, .main-menu nav > ul > li:hover > .mega-menu {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
  z-index: 999;
}
.main-menu--separate nav > ul {
  text-align: center;
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .main-menu--separate nav > ul {
    text-align: left;
  }
}
.main-menu--separate nav > ul > li > a {
  line-height: 100px;
}
.main-menu--separate .sub-menu {
  text-align: left;
}
.main-menu--separate .mega-menu {
  text-align: left;
}
.main-menu--separate .mega-menu.four-column {
  left: -300px;
}
@media only screen and (min-width: 1200px) and (max-width: 1499px) {
  .main-menu--separate .mega-menu.four-column {
    left: -300px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .main-menu--separate .mega-menu.four-column {
    left: -450px;
  }
}

/*----------  submenu  ----------*/
.sub-menu {
  position: absolute;
  left: -20px;
  top: 100%;
  background-color: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  width: 230px;
  visibility: hidden;
  opacity: 0;
  padding: 15px 0;
}
.sub-menu li {
  position: relative;
}
.sub-menu li a {
  color: #333333;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
  display: block;
  padding: 10px 15px;
}
.sub-menu li a:hover {
  color: #3EB65B;
}
.sub-menu li.menu-item-has-children > a::after {
  font-family: Ionicons;
  content: "";
  margin-left: 5px;
  float: right;
  font-weight: 300;
  font-size: 11px;
}
.sub-menu li:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
  z-index: 999;
}
.sub-menu .sub-menu {
  position: absolute;
  left: 100%;
  right: auto;
  top: 0;
  visibility: hidden;
  opacity: 0;
}
.sub-menu .sub-menu li a {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  color: #666;
  font-weight: 300;
  text-transform: capitalize;
  padding: 7px 15px;
}
.sub-menu .sub-menu li a:hover {
  color: #3EB65B;
}
.sub-menu .sub-menu .sub-menu {
  left: auto;
  right: 100%;
}
.sub-menu .sub-menu .sub-menu .sub-menu {
  left: 100%;
  right: auto;
}
.sub-menu .sub-menu .sub-menu .sub-menu {
  left: auto;
  right: 100%;
}

/*----------  mega menu  ----------*/
.mega-menu {
  position: absolute;
  left: 0;
  top: 100%;
  background-color: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  width: 100%;
  visibility: hidden;
  opacity: 0;
}
.mega-menu.four-column {
  width: 1100px;
  left: -100px;
  display: flex;
  flex-wrap: wrap;
  padding: 30px;
}
@media only screen and (min-width: 1200px) and (max-width: 1499px) {
  .mega-menu.four-column {
    width: 900px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .mega-menu.four-column {
    width: 900px;
    left: -200px;
  }
}
.mega-menu.four-column > li {
  width: 25%;
}
.mega-menu > li > a {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 15px;
  color: #333;
  font-weight: 600;
  text-transform: uppercase;
  color: #333;
  margin-bottom: 10px;
}
.mega-menu > li > a:hover {
  color: #3EB65B;
}
.mega-menu > li ul li a {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  color: #666;
  font-weight: 300;
  line-height: 26px;
}
.mega-menu > li ul li a:hover {
  color: #3EB65B;
}

.megamenu-banner a img {
  transition: 0.3s;
}
.megamenu-banner a:hover img {
  opacity: 0.7;
}

/*----------  mobile menu  ----------*/
.mobile-menu .mean-bar {
  position: relative;
  text-align: right;
  /*---- Mean Nav ----*/
}
.mobile-menu .mean-bar:before {
  position: absolute;
  top: 0;
  content: "MENU";
  left: 0;
  font-weight: 500;
  font-size: 16px;
  color: #363f4d;
  display: none;
}
.mobile-menu .mean-bar .meanmenu-reveal span {
  position: relative;
  /*---- Menu Open ----*/
