  font-size: 12px;
  line-height: 20px;
  opacity: 0;
  color: #333333;
  width: 20px;
  text-align: center;
  transition: all 0.3s ease 0s;
}
.login-form .check-box input[type=checkbox]:checked + label::before {
  border: 2px solid #333333;
}
.login-form .check-box input[type=checkbox]:checked + label::after {
  opacity: 1;
}

